import React from "react";
import { CircularGauge,Tooltip, Font, Scale, Geometry, SubvalueIndicator ,  RangeContainer, Range, } from 'devextreme-react/circular-gauge';


const gaugeMeter = (props) => {
  return (
    <CircularGauge
    id={props.id}
    value={Number(props.percent)}
    subvalues={props.subvalues}
  >
    <SubvalueIndicator
      type="triangleMarker"
      color="#8FBC8F"
    ></SubvalueIndicator>
    <Geometry
      startAngle={180}
      endAngle={0}>
    </Geometry>
    <Scale
      startValue={0}
      endValue={100}
      tickInterval={20}
    >
    </Scale>
    <Tooltip enabled={true} customizeTooltip={`${Number(props.percent)} % ` }>
          <Font size={10} color="#000000" />
        </Tooltip>
    <RangeContainer offset={10}>
          <Range startValue={0} endValue={30} color="#92000A" />
          <Range startValue={30} endValue={70} color="#E6E200" />
          <Range startValue={70} endValue={100} color="#77DD77" />
        </RangeContainer>
  </CircularGauge>
  );
};

export default gaugeMeter;
