import React from "react";
import { Line } from "react-chartjs-2";

const LineChart = (props) => {
  return (
    <div>
      <Line
        data={{
          labels: [
            "Jan",
            "Feb",
            "Mar",
            "Apr",
            "May",
            "Jun",
            "Jul",
            "Aug",
            "Sept",
            "Oct",
            "Nov",
            "Dec",
          ],
          datasets: [
            {
              label: "Cash Out",
              data: [7, 9, 13, 7, 5, 10, 12, 17, 12, 14, 19, 22],
              backgroundColor: ["rgba(0, 0, 0, 1)"],
              borderColor: ["rgba(0, 0, 0, 1)"],
              borderWidth: 1,
            },
          ],
        }}
        options={{
          maintainAspectRatio: false,
          scales: {
            /*  y: {
                  beginAtZero: true,
                }, */
            yAxes: [
              {
                gridLines: {
                  drawBorder: false,
                  display: false,
                  tickMarkLength: false,
                },
              },
            ],
          },
        }}
        height={150}
        /* width={700} */
      />
    </div>
  );
};

export default LineChart;
