import React from "react";

const CashFlow = (props) => {
  return (
    <table className="table" id="outerTable">
      <thead className="thead-dark">
        <tr>
          <th>Cash Flow Statement</th>
          <th>Unit</th>
          <th>2017</th>
          <th>2018</th>
          <th>2019</th>
        </tr>
      </thead>
      {props.cashFlowData.map((cashData, b) => {
        return (
          <tbody key={b} className="text-left mt-4">
            <tr>
              <td colSpan="5" className="underLine">
                {cashData.head !== undefined ? cashData.head : null}
              </td>
            </tr>

            {cashData.child.map((childData, childindx) => {
              return (
                <tr key={"childindx" + childindx}>
                  <td>{childData.name} </td>
                  <td>{childData.Unit}</td>
                  <td>{childData.firstYear}</td>
                  <td>{childData.secondYear}</td>
                  <td>{childData.thirdYear}</td>
                </tr>
              );
            })}
            <tr className="font-bold">
              <td>
                {cashData.total !== undefined ? cashData.total.name : null}
              </td>
              <td>
                {cashData.total !== undefined ? cashData.total.Unit : null}
              </td>
              <td>
                {cashData.total !== undefined ? cashData.total.firstYear : null}
              </td>
              <td>
                {cashData.total !== undefined
                  ? cashData.total.secondYear
                  : null}
              </td>
              <td>
                {cashData.total !== undefined ? cashData.total.thirdYear : null}
              </td>
            </tr>
          </tbody>
        );
      })}
    </table>
  );
};

export default CashFlow;
