import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";

const BarChart = (props) => {
  const [prepareData, setPrepareData] = useState({});

  useEffect(() => {
    createData();
  },[]);

  const createData = () => {
    let currentData = {
      labels: [],
      datasets: [],
    };
    let datasetData = {
      label: "Cash In",
      data: [],
      backgroundColor: ["rgba(0, 0, 0, 1)"],
      borderColor: ["rgba(0, 0, 0, 1)"],
      borderWidth: 1,
    };
    for (let data of props.chartData) {
      currentData.labels.push(data.name);
      datasetData.data.push(data.value);
    }
    currentData.datasets.push(datasetData);
    setPrepareData(currentData);
  };

  return (
    <div>
      <Bar
        data={prepareData}
        options={{
          maintainAspectRatio: false,
          scales: {
            y: {
              beginAtZero: true,
            },
          },
        }}
        height={150}
        /* width={700} */
      />
    </div>
  );
};

export default BarChart;
